        tr
          th.text-center FictionBook (.fb2). 
          td Es un formato abierto muy extendido de origen ruso. No acepta DRM. Define la estructura del libro con marcado semntico, por lo que es fcil convertido a otros formatos actuales o del futuro. Se recomienda para almacenar los libros de una biblioteca. Permite anotaciones y marcadores, y se adapta a la pantalla.
        tr
          th.text-center EPUB / EPUB3 (.EPUB, .EPUB3).
          td En principio, es un estndar (aunque con DRM necesita soporte de compaas como Adobe). Sin DRM, puede ser ledo por la mayora de dispositivos, salvo el Kindle de Amazon. El EPUB 3 es una evolucin del EPUB que permite una edicin ms compleja: anotado y marcado, funcionalidades propias de los audiolibros y sincronizacin de vdeo y texto, entre otros. A pesar de su intencin de convertirse en el formato estndar de <i>ebook</i>, an no est suficientemente aceptado.
        tr
          th.text-center Daisy (.dbt). 
          td Es el formato tpico de los audiolibros. Estn especialmente dirigidos a usuarios con discapacidad. El audio y el texto estn sincronizados, lo que permite la navegacin por el libro (ir a una pgina, hacer una bsqueda)
        tr
          th.text-center PDF (.pdf). 
          td Es un formato estndar muy aceptado y difundido. Los libros en PDF siempre conservan el mismo aspecto. Es decir, no se adaptarn a la pantalla ni se autopaginarn. pero los elementos se mantendrn en el mismo sitio, aunque se lean en distintos dispositivos. Admite notas y marcadores.
        tr
          th.text-center Comic Book (.cba, .cbr, .cbz). 
          td Es el formato tpico de los cmic, aunque sirve para cualquier libro con muchas imgenes. Para optimizar el peso, se comprimen. La extensin depende de la compresin: .cbz para ZIP; .cbr para RAR y .cba para ACE, aunque admite otro tipo de formatos de compresin. No es interactivo, no permite las anotaciones ni se adapta a la pantalla.
        tr
          th.text-center Open eBook (.opf). 
          td Es el origen de los EPUB y constituye uno de sus elementos. Se trata de un formato estndar avalado por la NIST.
        tr
          th.text-center Palm Reader (.pml).
          td Es el formato tpico del lenguaje de marcado creado por Palm Reader. Se puede abrir con eReader.
        tr
