          td Es el formato de texto enriquecido. Microsoft lo cre para poder dar caractersticas al texto (cursivas. tipo de fuente, tamao). La mayora de procesadores de texto leen y escriben este formato, y prcticamente todos los lectores lo soportan. Por su sencillez, no tiene muchas funcionalidades propias del libro electrnico.
        tr
