        div.p-4.mb-4(style="background-color: #FFF5E5")
          p.mb-0  El diseo y desarrollo <i>web</i> debe fundamentarse en el tipo de personas a quienes se dirige el contenido, as como en sus necesidades, respecto al producto o servicio. Por eso es importante que el usuario final sea involucrado en el proceso, conociendo sus opiniones desde etapas muy tempranas del proyecto.
        p.mb-0 Para lograr este cometido, existen varios mtodos y tcnicas, como las pruebas de usuarios, el seguimiento ocular, el <i>Card sorting</i>, las entrevistas personales, los grupos focales (<i>eye tracking</i>), los heursticos que son los procesos cognitivos que inconscientemente la persona automatiza al momento de elegir algo, entre otros.
