          p.mb-4 La arquitectura de la informacin va muy ligada al concepto <font class="color-1"><b>UX</b></font> (<i>User Expirence</i> en ingls, o experiencia de usuario) y se puede definir como la organizacin de todas las partes de algo, para que sea lo ms comprensible posible. 
          b.mb-0 La arquitectura de la informacin busca bsicamente el ordenamiento de todos los datos disponibles para que el usuario de manera lgica y ordenada realice la bsqueda de forma ms rpida, fcil, gil y dinmica.
