      .col-lg-5.d-flex.align-items-center.p-4(style="background-color:#E2F4F6")
        p.mb-0 Un ejemplo claro de cmo funciona la arquitectura de informacin, es compararlo con el manejo de una biblioteca, donde cada libro pertenece a una categora especfica, y se dispone en un espacio delimitado para ser ubicado de manera sencilla; los libros de historia se encuentran en una estantera delimitada, los de cocina en otra, los de poltica en un lugar diferente y as sucesivamente; estos tomos pueden ser ordenados de acuerdo con su importancia, su volumen o tamao; de la misma manera pasa en la arquitectura de la informacin, busca que los datos presente una estructura fcil y comprensible para todo el pblico o para su gran mayora. 

