            p.mb-0 Este ordenamiento se puede realizar por medio de categoras de productos, contenidos, palabras clave etc.  Es fundamental que la informacin que se contenga, est estructurada y sea comprensible, que tenga una lgica de lectura, que permita identificar elementos y sus reacciones, y que puedan tener en cuenta las interacciones y usabilidad de los usuarios.

    .row.justify-content-center.mb-5(data-aos="zoom-in-up")
      .col-lg-7
        p.mb-4 Se puede afirmar que cuando se habla de la arquitectura de la informacin se debe pensar en jerarquas, categoras y todos los elementos que puedan estar a beneficio de los usuarios, de su buena navegacin e interactividad; es importante que se aprecie como un recurso indispensable, ya que a primera vista podra parecer un tema sin mucha relevancia, y su importancia va ms all de una simple organizacin. 
        p.mb-4 Segn Steven Krug autor del libro <b>No me Hagas Pensar</b>; existen cuatro preguntas que el usuario continuamente hace al momento de ingresar a un sitio
        figure.mb-0
          img(src='@/assets/curso/tema5/img04.svg') 
