    p.mb-4 Son estas preguntas las que se deben empezar a responder, desde la parte de diseo y funcionalidad, dndole al usuario la posibilidad de orientarse y a su vez ofrecer la mejor calidad al momento de navegar y usar la pgina o el documento electrnico. 

    .row.justify-content-center.mb-5(data-aos="zoom-in")
