        div
          p.mb-4 De esta forma se evitan daos como las migraciones de usuarios insatisfechos o quejas por reclamos de fallas en funcionalidades o malas interconexiones, esto no solo genera un mal concepto de marca, sino que genera confusin y duda sobre sus acciones, generando y asociando sensaciones negativas que desestiman y desvalorizan la compaa.
          div.p-4.mb-4(style="background-color: #FFF5E5")
            p.mb-0 <b>Teniendo en cuenta las preguntas a las que se debe reaccionar se deben entender estos conceptos que ayudarn a enfatizar los esfuerzos requeridos en cada uno de los campos a reaccionar, estos conceptos sern pilares que variarn segn las necesidades, el negocio o los logros a alcanzar.</b>
          p.mb-0 Por tanto, se pueden resumir estos tres grandes temas que hacen parte de la IA (<i>Information Architecture</i>, por sus siglas en ingls) o Arquitectura de la informacin.

