    p.mb-5 Realizado el ejercicio anterior y entendida la definicin de IA, se enfocarn en estos tres pasos que sern fundamentales para ayudar a los usuarios a responder las preguntas anteriormente planteadas.

    .tema-5
      AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
        .row.justify-content-end(titulo="a. Realizar un inventario")
          .col-lg-8.ps-5
            div
              p.mb-4 El primer paso, ser identificar todos los elementos que se quieren incluir, partiendo del contenido de cada seccin como las funcionalidades que se quieren ofrecer.
              p.mb-4 Para saber cul ser el contenido a incluir, ser necesario basarse en las investigaciones y el conocimiento obtenido sobre los usuarios al igual que toda la informacin del sitio <i>web</i>, de la aplicacin o del elemento digital, es importante escuchar siempre al cliente y saber cuales son sus demandas y sus expectativas frente al proyecto. 
              p.mb-0 Cada proyecto posee una IA distinto, dado que su contenido y objetivos son diferentes, as que ser importante conocer las diferentes metodologas, para ajustar el proyecto a la que ms se adapte.
          .col-lg-4
            figure.mb-0(data-aos="zoom-in-up")
              img(src='@/assets/curso/tema5/img09.png') 
        .row.justify-content-center(titulo="b. Agrupar las tarjetas (<i>Card Sorting</i>)")
          .col-lg-8.ps-5
            div
              p.mb-4 En este paso se establecen las relaciones entre los tems inventariados anteriormente, en este paso se deciden cuales se agrupan en secciones o mens y estos si estn en otros grupos sucesivamente.
              p.mb-4 La clave es poder definir un camino para poder llevar a los usuarios cuando quieren ir de un punto A, a un punto B, o C, etc.
              p.mb-4 Una buena idea, es realizar preguntas o hacer una encuesta para saber y entender como el usuario o pblico objetivo interacta y utilizan de alguna u otra forma un producto determinado. 
              p.mb-0 Una buena tcnica para poder hacer esa seleccin es el poder simular cada momento inventariado anteriormente en una tarjeta, las cuales despus de tener todos y cada uno los elementos inventariados, se puedan colocar en una mesa y con ayuda de varios asistentes, pedirles que realicen sus agrupaciones segn sus procesos mentales; cada uno de estos procesos sern diferentes y darn resultados que ayudarn a comprender otros puntos de vista de usuarios que no se tenan presupuestados; finalmente se encontrarn los patrones adecuados para poder encajar a todos y poder satisfacer de la mejor manera al usuario. 
          .col-lg-4
            figure.mb-0(data-aos="zoom-in-up")
              img(src='@/assets/curso/tema5/img10.png') 
        .row.justify-content-center(titulo="c. Definir el mapa")
          .col-lg-8.ps-5
            div
              p.mb-4 Esta tercera etapa, consiste en ubicar todos los elementos anteriormente inventariados y agrupados, en un mapa que permitir ver y reflejar las relaciones y agrupaciones establecidas entre todos los contenidos y elementos.
              p.mb-0 En algunas ocasiones ser necesario renombrar o recategorizar algunas, o por el contrario cambiar de posicin, todo por encontrar lo mejor al momento de pensar en la usabilidad de los usuarios.  
          .col-lg-4
            figure.mb-0(data-aos="zoom-in-up")
              img(src='@/assets/curso/tema5/img11.png') 

    p.mb-4 Entre las metodologas aplicadas en la arquitectura de la informacin se puede encontrar:
    .tema-5.mb-5
      .row.justify-content-center
        .col-lg-7
          LineaTiempoD.color-acento-botones
            div(numero="1" titulo="Estructura Jerrquica")
              p.mb-4  Es de las estructuras ms utilizadas, tiene forma de rbol y desde su raz descienden todos sus elementos, su principal beneficio para los usuarios es poder saber siempre donde est ubicado, permitiendo recrear mapas mentales de las estructuras de los sitios, en este tipo de estructuras es natural que los usuarios deban navegar sobre sus pasos para llegar a las cabezas de los mens, lo que no permite la libre navegacin de los sitios.  
              .row.justify-content-center
                .col-lg-4
                  figure.mb-0
                    img(src='@/assets/curso/tema5/img13.svg')

            div(numero="2" titulo="Navegacin Hipertexto")
              p.mb-4  En este tipo de estructura, la informacin se organiza de forma lineal, la informacin est conectada por cualquier tipo de asociacin, sea jerrquica o no; la flexibilidad de sus estructuras, permiten realizar ms relaciones entre otros elementos y mens, siendo a la larga un dolor de cabeza para muchos usuarios que son nuevos en las pginas, ya que la complejidad de sus recorridos hace que mucha informacin se pierda si no se trata con el adecuado cuidado. 
              .row.justify-content-center
                .col-lg-9
                  figure.mb-0
                    img(src='@/assets/curso/tema5/img14.svg')

            div(numero="3" titulo="Navegacin lineal o plana")
              p.mb-4 Es la estructura ms simple, donde de un bloque pasa otro o al anterior y as las veces que necesite, es especial si necesitamos que el usuario haga un avance obligado por todas las pginas, sin que ningn otro bloque le cause distraccin y solo se presenten estas oportunidades al final o al inicio de cada una.  
              .row.justify-content-center
                .col-lg-5
                  figure.mb-0
                    img(src='@/assets/curso/tema5/img15.svg')
