    #t_5_1.titulo-segundo.color-acento-contenido
      h2 5.1 Evaluacin heurstica o usabilidad

    .row.justify-content-start.mb-4(data-aos="zoom-in-up")
      .col-lg-6        
        p.mb-5 Luego de la implementacin de la arquitectura de informacin por medio del uso de tarjetas, el proceso de evaluacin heurstica o metodologa de inspeccin en experiencia de usuario busca identificar y resolver aquellas problemticas presentes en la usabilidad de los componentes de un producto, teniendo como principal objetivo medir la facilidad de uso, que se centra en el conocimiento del diseo y la experiencia de usuario. 
        .texto-largo-adorno.bg--2.px-4.py-2.mb-5(data-aos="fade-right")
          h3.mb-0.me-3 El declogo de principios heursticos de Jakob Nielsen
             
        
        p.mb-0 Este declogo desarrollado por Jakob Nielsen, reconocido experto mundial en usabilidad de la <i>web</i>, da diez principios heursticos, centrados en mejorar la experiencia de usuario, en el desarrollo de posible resolucin de problemas.        
      .col-lg-6.d-flex.align-items-center
        .imagen-adornos-flotantes
          img(src='@/assets/curso/tema5/img17.png') 
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left:; right:  0% ; top: ; bottom: 0% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left:  0% ; right:; top:0% ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left:  -5% ; right: ; top: ; bottom: 10% ; width: 60px ")      
    
    p.mb-5 Las heursticas o el conjunto de tcnicas o mtodos para resolver un problema del griego hallar, inventar, son una serie de elementos a tener en cuenta al momento de evaluar el diseo de las interfaces de usuario, recopiladas por el ingeniero Jakob Nielsen:

    .row.justify-content-start.mb-5(data-aos="zoom-in-up")
      .col-lg-4
        .imagen-adornos-flotantes
          img(src='@/assets/curso/tema5/img18.png') 
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left:; right:  0% ; top: ; bottom: 0% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left:  0% ; right:; top:20% ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left:  -5% ; right: ; top: ; bottom: 10% ; width: 60px ")      
      .col-lg-8
        ul.lista-ul--color.mb-4               
          li 
            i.fas.fa-square 
            | Visibilidad del estado del sistema.
          li 
            i.fas.fa-square 
            | Utilizar el lenguaje de los usuarios.
          li 
            i.fas.fa-square 
            | Control y libertad para el usuario. 
          li 
            i.fas.fa-square 
            | Consistencia y estndares.
          li 
            i.fas.fa-square 
            | Prevencin de errores.
          li 
            i.fas.fa-square 
            | Minimizar la carga de la memoria del usuario.
          li 
            i.fas.fa-square 
            | Flexibilidad y eficiencia de uso.
          li 
            i.fas.fa-square 
            | Diagnsticos estticos y diseo minimalista.
          li 
            i.fas.fa-square 
            | Ayudar a los usuarios a reconocer, diagnosticar y recuperarse de los errores. 
          li 
            i.fas.fa-square 
            | Ayuda y documentacin.
        
        p.mb-0 Para aplicar de manera objetiva los principios de usabilidad, se debe dar una calificacin o medida a cada uno de los componentes; por ejemplo, aquellos que no cumplen con las necesidades requeridas se califican  con cero(0), los que van en proceso, pero an tienen falencias se califican con un valor de tres (3), y los que cumplen con todos los estndares de calidad con cinco (5), esto con el objetivo de realizar las mejoras respectivas. 

    .texto-largo-adorno.bg--2.px-4.py-2.mb-5(data-aos="fade-right")
            h3.mb-0.me-3 Ventajas e inconvenientes de la evaluacin heurstica
              
    
    p.mb-5 Como cualquier metodologa, la evaluacin heurstica posee ventajas y desventajas.

