    #t_5_2.titulo-segundo.color-acento-contenido
      h2 5.2 Flujogramas

    .row.justify-content-center.mb-5(data-aos="zoom-in-up")
      .col-lg-4.d-flex
        p.mb-0 Los flujogramas o diagramas de flujo, es un esquema o una representacin grfica de un proceso, en el cual se muestran el orden y estructura del mismo, son muy importantes en la elaboracin de guiones de productos multimedia ya que permiten una proyeccin de la estructura y la organizacin del producto o aplicacin; la relacin de todos sus elementos facilita comprender cada una de las partes y sus componentes, de acuerdo con el grado de importancia y jerarqua que poseen. 
      .col-lg-3(style="background-color:#E2F4F6")
        figure.mb-0
          img(src='@/assets/curso/tema5/img21.png') 
      .col-lg-5.d-flex.align-items-center(style="background-color:#E2F4F6")
        div 
          p.mb-4 Existen diferentes tipos de flujogramas, se encuentran en formato vertical, horizontal, panormico, arquitectnico y de bloques. Las principales ventajas de este tipo de elementos es que ayudan a conectar diversas ideas en torno a la productividad del proyecto a ejecutar, al ser un elemento tan visual, es de mejor entendimiento para el equipo de produccin. 
          b.mb-0 En su realizacin se van presentando los problemas y oportunidades y conexiones que surgen a partir del proceso de observacin, los que sern ubicados en estos diagramas.
    
    p.mb-5.text-center Los flujogramas utilizan smbolos y convenciones para su mayor entendimiento y uso, ver tabla 3.
    
